.popup__container {
    width: 430px;
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    position: relative;
    padding: 34px 36px 37px 36px;
    box-sizing: border-box;
    cursor: default;
}

@media screen and (max-width: 600px) {
    .popup__container {
        width: 282px;
        padding: 25px 22px;
    }
}