.popup__close {
    width: 32px;
    height: 32px;
    box-sizing: border-box;
    background: none;
    border: none;
    background-image: url(../../../images/close-Icon.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: -40px;
    right: -40px;
    cursor: pointer;
}

.popup__close:hover {
    opacity: 0.6;
    transition: 0.3s;
}

@media screen and (max-width: 600px) {
    .popup__close {
        width: 20px;
        height: 20px;
        top: -36px;
        right: 0;
    } 
}